import { Component, OnInit, OnChanges,ApplicationRef,SimpleChanges,SimpleChange, Input } from '@angular/core';
import { ProductServiceService } from '../../../shared/services/product-service.service';
import { stringify } from '@angular/core/src/util';
import { identifierModuleUrl } from '@angular/compiler';
import { noUndefined } from '@angular/compiler/src/util';
@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {
  constructor(private products:ProductServiceService,private changeDetectorRef:ApplicationRef) { }

  private _cartProduct:JSON;
  previousJson:JSON;
  presentCartJson :JSON;
  i:number=0;
  totalPrice:number=0;

  get cartProduct(): JSON {
    return this._cartProduct;
  }
  
  @Input()
  set cartProduct(val: JSON) {
    this.i++;
    let previousProduct = JSON.stringify(this._cartProduct);
    let currentSelection = JSON.stringify(val);
    let combined:string;
    console.log('previous item = ', previousProduct);
    console.log('currently selected item=', currentSelection);
    if(previousProduct !==undefined ){
      previousProduct = previousProduct.replace(']','');
    }else if(previousProduct!== '{}'){
      previousProduct ='';
    }
    if(currentSelection !=undefined){
      currentSelection =currentSelection.replace('[','');
    }
    

    combined = previousProduct+','+currentSelection;
    combined =combined.trim();
    if(this.i>2){
      console.log("this.presentCartJson");
      console.log(combined);
      this.presentCartJson = JSON.parse(combined);
    }
    if(this.i>2){
      this._cartProduct = this.presentCartJson
      this.getTotalPrice();
    }else{
      this.presentCartJson=val;  
      this._cartProduct = val;
      this.getTotalPrice();
    }
   
  }

  getTotalPrice(){

    if(this.presentCartJson != undefined){
    var carProduct = Object.values(this.presentCartJson);
  this.totalPrice=0;  
    for (let i = 0; i < carProduct.length; i++) {
      console.log("newdata[i]");
      
      this.totalPrice += carProduct[i].price;
      }
    }
  }
  ngOnInit() {
    this.products.currentProduct.subscribe(selectedProduct =>this.cartProduct=selectedProduct);
    }
}
