import { Injectable,ApplicationRef } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable,BehaviorSubject } from 'rxjs';
  

@Injectable({
  providedIn: 'root'
})
export class ProductServiceService {
  
  private selectedCartProduct = new BehaviorSubject(JSON);
  currentProduct = this.selectedCartProduct.asObservable();
  constructor(private http:HttpClient,private changeDetectorRef:ApplicationRef ) { }
  

assignToCart(product){
  this.selectedCartProduct.next(product);
  this.changeDetectorRef.tick();
  }
  
  getProducts(){ 
    console.log("Products JSON");
    console.log(this.http.get('http://localhost:8080/products'));
    return this.http.get('http://localhost:8080/products');

  }


  
}
