import { Component, OnInit,EventEmitter, Input, Output } from '@angular/core';
import { ProductServiceService } from '../../../shared/services/product-service.service';
@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css']
})
export class ProductListComponent implements OnInit {
  cartProduct:JSON;
  productSeleted:JSON
  // @Output() addcartProduct = new EventEmitter<JSON>();
  constructor(private products:ProductServiceService) { }

  
  ngOnInit() {
    this.products.currentProduct.subscribe(selectedProduct =>this.cartProduct=selectedProduct);
  }

  checkData(cartJson) {
    this.productSeleted = cartJson;
   this.propogateData(this.productSeleted);
   // this.addcartProduct.emit(cartJson);   
  }	

  propogateData(productSeleted){
    this.products.assignToCart(productSeleted);
  }
}
